.app-list-item{
    font-size: 1.25rem;
}

.app-list-item button{
    width: 35px;
    height: 35px;
    margin: 3px;
    font-size: 17px;
    border: none;
    cursor: pointer;
}

.app-list-item button:focus{
    box-shadow: none;
    outline: none;
}

/* .app-list-item .btn-star{
    color: #ffd700;
} */

.app-list-item .btn-trash{
    color: red;
}

.app-list-item .fa-heart{
    width: 35px;
    height: 35px;
    text-align: center;
    line-height: 35px;
    color: gray;
    transition: 0.3s all;
    transform: translateX(30px);
    opacity: 0;
}

.app-list-item-label{
    display: block;
    line-height: 35px;
    cursor: pointer;
    user-select: none;
    transition: 0.5s all;
}

.app-list-item .fa-heart{
    opacity: 1;
    transform: translateX(0px);
}

.app-list-item.important .app-list-item-label {
    color: #ffd700;
}

.app-list-item.done .app-list-item-label {
    text-decoration: line-through;
}

.app-list-item .app-list-item-label {
    color: #aeaeae;
}

.btn-star {
    color: #aeaeae;
}

.btn-star.active {
    color: #ffd700;
}

.app-list-item.like .fa-heart {
    color: red;
}